@mixin mainAnimation {
	transition: all 200ms ease-out;
}

body {
	background: #60A0E7;
}


// Barra de Pesquisa
#SearchBox input, #SearchBox button {
	color           : black;
	background-color: white;
	border-radius   : 20px;
	border-style    : none;
	outline         : none;
	text-align      : center;
	margin          : 1% 0;
	display         : inline-block;
	user-select     : default;
	@include mainAnimation;
}

#SearchBox input {
	width: 75%;
}

#SearchBox input::placeholder {
	color     : #ccc;
	text-align: center;
}

#SearchBox > *:hover , #SearchBox > *:focus {
	box-shadow: 0px 5px 15px rgba(0,0,0,.25);
	@include mainAnimation;
}

#SearchBox button:hover, #SearchBox button:focus {
	@include mainAnimation;
}

#SearchBox button svg {
	transform: rotate(20deg);
	@include mainAnimation;
}

#SearchBox button:hover > svg, #SearchBox button:focus > svg {
	transform: scale(2.25) rotate(-225deg);
	@include mainAnimation;
} 

#SearchBox button {
	padding: 0.5% 0.9%;
}

#MenuIcons {
	margin-top: 10%;
	display   : inline;
}

 #MenuIcons svg {
 	margin-left: 4%;
 	cursor: pointer;
 	@include mainAnimation;
 }

#MenuIcons svg:hover, #MenuIcons svg:focus {
	transform: scale(1.5);
	fill: white;
}

#MenuIcons svg, #MenuIcons span, #SearchBox {
	fill         : #626262;
	outline-style: none;
	user-select  : none;
}

 // Menus
.TopMenu {
	position        : absolute;
	right           : 0;
	background-color: #F7F7F7;
	border-radius   : 0 0 13px 13px;
	box-shadow      : 0px 2px 2px rgba(0,0,0,.4);
	text-align      : left;
	margin-bottom   : 10%;
	transition      : all .5s ease;
} 

.TopMenu ul {
	margin-left: -5%;
	list-style : none;
	user-select: none;
	outline    : none;
	width      : 100%;
}

.TopMenu li {
	width  : 100%;
	padding: 5% 0 5% 5%;
	margin : 5% 0 0 -5%;
	outline: none;
	cursor : pointer;
	@include mainAnimation;
}

.TopMenu li:focus, .TopMenu li:hover {
	background   : rgba(0,0,0,.1);
	border-radius: 10px; 
	@include mainAnimation;
}

.TopMenu li span {
	color         : black;
	font          : bold 16px Arial;
	margin-left   : 8%;
	vertical-align: middle;
}

.TopMenu img {
	max-width: 15%;
	padding  : 0;
}

.TopMenu:first-of-type ul span {
	margin-left: 4%;
	font: normal 12px Arial;
}

.TopMenu:first-of-type name {
	margin-left: 2%;
	font       : normal 12px Arial;
	color      : rgba(70,178,221,1);
}

.TopMenu:first-of-type {
	max-height   : 43vh;
	overflow     : auto;
	overflow-x   : hidden;
	border-radius: 0 0 0 13px;
}

.TopMenu::-webkit-scrollbar   {
	background-color: transparent;
	width           : 5px;
	border-radius   : 12px;
}

 .TopMenu::-webkit-scrollbar-thumb  {
	background-color: #58E5E8;
	width           : 2px;
 }

 .TopMenu li:last-of-type {
 	margin-bottom: 5%;
 }

 // RodapÃ©
#Footer {
	position        : absolute;
	width           : 100%;
	min-height      : 18vh;
	bottom          : 0;
	left            : 0;
	overflow        : hidden;
	background-color: #022F38;
}

h3 {
	color: white;
	font : normal 18px Verdana;
}

#Footer h3, #Footer img, #Footer a, #Footer span, #Footer svg {
	user-select  : none;
	outline-style: none;
}

#Footer p {
	color     : white;
	font-style: normal;
	font-size : 16px;
	text-align: left;
}

#Footer ul {
	list-style: none;
}

#Footer span:first-of-type:hover > a img  {
	opacity: .6;
	filter : blur(5px);
	@include mainAnimation;
}

#Footer div > img {
	position : absolute;
	top      : 0;
	left     : 0;
	transform: scale(1) translate(-65%,-35%);
}

#Footer span img {
	width      : 10%;
	cursor     : pointer;
	margin-left: 0%;
	text-align : center;
	@include mainAnimation;
}

#Footer span img:first-of-type, #Footer span a:first-of-type {
	margin-left: 0;
}

#Footer span a {
	margin-left: 5%;
}

#Footer span img:hover, #Footer a:focus img {
	transform: scale(1.35);
	opacity  : 1!important;
	filter   : blur(0px)!important;
}



@media(max-width: 970px) {
	#Footer, #TopBar, .TopMenu {
		position: relative;
	}

	.TopMenu:first-of-type {
		max-height: 75vh;
	}

	.TopMenu ul {
		margin-left: 0;
	}
}
